@import './variables';

.landing-body{
  width: 100%;
  margin: 0 auto;
  padding: 50px 0;
}
button:hover,
button:focus{
  cursor: pointer;
  outline: none;
}
.call-to-action{
  width: 100%;
  max-width: 1200px;
  margin: auto;
}
.call-buttons{
  margin-left: 20px;
}
.call-buttons button{
  background-color: white;
  border: 1px solid black;
  padding: 4px 7px;
  border-radius: 9px;
  margin-left: 10px;
}
.call-buttons button:hover,
.call-buttons button:focus{
  background-color: $pinkish;
  border: 1px solid $pinkish;
  color: white;
  transition: all 0.7s ease-in-out;
}
.call-products{
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  padding: 10px;
}
.call-products figure{
  border: 1px solid black;
  padding: 7px;
  border-radius: 5px;
  margin-top: 12px;
  box-shadow: 2px 3px 9px #7d747b;
  transition: transform .5s;
}
.call-products figure:hover{
  cursor: pointer;
  transform: scale(1.05);
}
.call-products figcaption{
  padding: 8px 16px;
  text-align: center;
  background-color: rgb(240, 238, 238);
}
.call-products figcaption p{
  padding: 3px 0px;
  font-weight: bolder;
}
.call-products figcaption button{
  padding: 5px 10px;
  border: 1px solid $pinkish;
  border-radius: 6px;
  color: white;
  background-color: $pinkish;
}
.call-products figcaption button:hover{
  background-color: white;
  color: $pinkish;
  transition: all 0.7s ease;
}
img.product{
  height: 310px;
  width: 250px;
  margin-top: 10px;
  border-radius: 5px;
}

.modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  padding-top: 100px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.9); /* Black w/ opacity */
}
.modal-content {
  margin: auto;
  display: block;
  width: 80%;
  max-width: 700px;
  height: inherit;
}
.modal-content,
#caption {
  animation-name: zoom;
  animation-duration: 0.6s;
}
@keyframes zoom {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}
.close {
  position: absolute;
  top: 15px;
  right: 35px;
  color: #f1f1f1;
  font-size: 40px;
  font-weight: bold;
  transition: 0.3s;
}

.close:hover,
.close:focus {
  color: #bbb;
  text-decoration: none;
  cursor: pointer;
}

/* 100% Image Width on Smaller Screens */
@media only screen and (max-width: 700px) {
  .modal-content {
    width: 100%;
  }
}
